import React from "react";
import "./reviewPage.css";
import { Link } from "react-router-dom";


function reviewPage(props) {
  const {
    logo,
    homeText,
    genresText,
    languageText,
    loginText,
    registerText,
    icon,
    footerText1,
    footerText2,
    submitText,
    inputPlaceholder,
    inputType,
  } = props;

  return (
    <div className="PageCenter">
      <div className="reviewPage screen">
        <div className="reviewPageContainer"></div>
        
            {/* Header */}
            <header>
                <div className="header"> 
                    <img className="headerLogo" src={logo} />
                    <div className="clickable">
                    <div className="languageContainer"></div>
                    <div className="languageText">{languageText}</div>
                    </div>
                    <div>
                    <Link to="/home-page">
                        <div className="homeText darkergrotesque-medium-white-35px2">{homeText}</div>
                    </Link>
                    </div>
                    <div>
                    <Link to="/genresPage">
                        <div>
                        <div className="genresText darkergrotesque-medium-white-35px2">{genresText}</div>
                        </div>
                    </Link>
                    </div>

                {/* registerd user */}
                <ul>
                    <img  className="regUserIcon" src="/img/regUser.png"/>
                    <li className="dropdown">
                        <a className="dropbtn">Username</a>
                        <div className="dropdownContent">
                        <a className="logout">Logout</a>
                        </div>
                    </li>
                </ul>

               </div>
        </header>

            {/* main */}
            <main>
            <div className="reviewBody"></div>
            <div>
                <div className="reviewBox"></div>
                <textarea className="writeReview" name="writeReview" placeholder={inputPlaceholder} rows="5" cols="73"> 
                    {/* <input className="writeReview" name="writeReview"  placeholder={inputPlaceholder} type={inputType} /> */}
               
                </textarea>
                <div className="submitButton">
                <Link to="/home-page">
                <div className="submitButtonContainer">
                        <div className="submitText">{submitText}</div>
                </div>
                </Link>
                </div>
            </div>
            </main>

            {/* footer */}
            <footer>
            <div className="footer"> </div>
                <img className="footerLogo" src={logo} />
                <div className="footerText1">{footerText1}</div>
                <div className="copyRightText inter-light-bon-jour-35px2">
                <span>{footerText2}</span>
                </div>
            </footer>
        
        </div>
      </div>
  );
}

export default reviewPage;
